document.addEventListener('DOMContentLoaded', function() {
    iniciarApp();
});

function iniciarApp() {
    darkMode();
    mostrarMenu();
}

function darkMode() {

    const preferencia = window.matchMedia('(prefers-color-scheme: dark)');
    
    const botonDark = document.getElementById("link-moon");
    const body = document.querySelector('body');

    preferencia.addEventListener('change', function () {
        if (preferencia.matches) {
            body.classList.add('dark');
        } else {
            body.classList.remove('dark');
        }
    })

    if (preferencia.matches) {
        body.classList.add('dark');
    } else {
        body.classList.remove('dark');
    }

    botonDark.addEventListener('click', function(e){
        e.preventDefault();
        body.classList.toggle('dark');
    })
}

function mostrarMenu() {
    const botonMenu = document.getElementById('link-menu');
    const navegacion = document.getElementById('navegacion')
    const body = document.querySelector('body');

    
    botonMenu.addEventListener('click', function(e) {
        e.preventDefault();
        navegacion.classList.toggle('mostrar-navegacion')
        navegacion.classList.toggle('header__navegacion')

    })
}